import React, { ReactNode } from 'react';
import BaseComponent from '../../../../base/base-component';
import { SODGradeColor } from '../../../../global/grade-color-enum';
import { ISimilarStatesItemProperties } from './item-interface';
import './item.scss';

export default class SimilarStatesItem extends BaseComponent<ISimilarStatesItemProperties> {
  public render(): ReactNode {
    const { item, isActive = false } = this.props;
    const rootClassName = ['c-similar-box', isActive ? 'is-active' : ''].join(
      ' '
    );

    return (
      <div
        className={rootClassName}
        style={{ backgroundColor: SODGradeColor[item.grade] }}
      >
        <div className="c-similar-box__info">
          <div>{item.abbreviation}</div>
          <div>{item.title}</div>
        </div>
        <div className="c-similar-box__rate">{item.score}</div>
      </div>
    );
  }
}
